import Vue from 'vue';
import Router from 'vue-router';
import App from "../App"
const index = r => require.ensure([], () => r(require('../pages/index')), 'index')
const newNote = r => require.ensure([], () => r(require('../pages/newNote')), 'newNote')
const search = r => require.ensure([], () => r(require('../pages/search')), 'search')

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
      {
        path: '',
        redirect: "/index"
      },{
        path: "/index",
        component: index
      },{
        path: "/newNote",
        component: newNote
      },{
        path: "/search",
        component: search
      }]
    },
  ],
});



